<template>
	<yd-layout>
		<div v-if='showLogin' class="Login">
			<div class="logo flex flex-x-center">
				<img src="../assets/imgs/logo_ysh@2x.png" alt="">
				<!--<img src="../assets/imgs/logo@2x.png" alt="">-->

			</div>

			<div class="form">
				<div class="flex form-group">
					<img src="../assets/imgs/login_icon_user@2x.png" alt="" class="pswicon">
					<yd-input style="padding-left: 10px" type="text" v-model="username" :show-success-icon="false" max="20" placeholder="请输入用户名"></yd-input>
				</div>
				<div class="flex form-group">
					<img src="../assets/imgs/login_icon_password@2x.png" alt="" class="pswicon">
					<yd-input style="padding-left: 10px" type="password" :show-success-icon="false" v-model="password" max="20" placeholder="请输入密码"></yd-input>
				</div>
				<div class="flex flex-x-end forget">忘记密码？</div>
				<div class=" flex flex-x-center">
					<div @click="login" class="loginbtn flex flex-y-center flex-x-center">
						<span>登录</span>
					</div>
				</div>
			</div>
			<div v-if='false' class="reg">还没有账号？
				<span @click="reg">立即注册</span>
			</div>
		</div>
	</yd-layout>
</template>
<script>
	import configs from '../configs'
	export default {
		data() {
			return {
				showLogin: false,
				openId: '',
				username: '',
				password: ''
			}
		},
		created() {
			console.log(window.location.href.indexOf('?'))
			if(window.location.href.indexOf('?') >= 0) {
				var code = window.location.href.split('?')[1].split('&')[0]
				var o = {}
				o[code.split('=')[0]] = code.split('=')[1]
				this.postRequest('customer/wxAuthor', {
					code: o.code
				}).then(res => {
					console.log('wxAuthor', res)
					if(res.data.state == 0 && res.data.aaData.openid) {
						this.openId = res.data.aaData.openid
						this.postRequest('', {
							openId: res.data.aaData.openid,
							systemId: 8,
							flag: 1,
							authCode: '',
						}, true).then(resp => {
							console.log('login', resp)
							if(resp && resp.status == 200 && resp.data.state == 0) {
								if(resp.data && resp.data.state == 0) {
									let userInfo = resp.data.sessionUser
									userInfo.token = resp.data.token
									if(userInfo.typeId == 'buyer') {
										this.$store.commit('login', userInfo);
										this.$root.userInfo = JSON.parse(localStorage.getItem('userInfo'))
										this.$router.push({
											name: 'Enshrine'
										})
									} else {
										this.$store.commit('login', userInfo);
										this.$root.userInfo = JSON.parse(localStorage.getItem('userInfo'))
										this.$router.push({
											name: 'Merchant',
											query: {
												sellerId: resp.data.sessionUser.typeCode
											}
										})
									}
								}
							} else {
								this.showLogin = true
							}
						})
					} else {
						this.showLogin = true
					}
				})
				console.log(code)
				console.log(o)
			} else {
				this.showLogin = true
			}

		},
		mounted() {
			this.$store.dispatch('changeNavBarTitle', '登录')
		},
		methods: {
			/**
			 * 注册
			 */
			reg() {
				this.$router.push({
					name: 'UserReg'
				})
			},
			/**
			 * 登录
			 */
			login() {
				if(this.username == '') {
					this.$dialog.alert({
						mes: '请输入用户名'
					});
					return false
				}
				if(this.password == '') {
					this.$dialog.alert({
						mes: '请输入密码'
					});
					return false
				}

				this.postRequest('', {
					loginName: this.username,
					password: this.password,
					systemId: 8,
					flag: 1,
					authCode: '',
				}, true).then(resp => {
					if(resp && resp.status == 200) {
						if(resp.data && resp.data.state == 0) {
							let userInfo = resp.data.sessionUser
							userInfo.token = resp.data.token
							if(userInfo.typeId == 'buyer') {
								this.$store.commit('login', userInfo);
								this.$root.userInfo = JSON.parse(localStorage.getItem('userInfo'))
								//后期和下面的一起修改。代码太多了
								this.postRequest('user/update', {
									wxopenid :this.openId,
									userId:resp.data.sessionUser.userId,
									token: resp.data.token,
								},false,true).then(res => {
									console.log(res)
									this.$router.push({
										name: 'Enshrine'
									})
								})
								
							} else {
								this.$store.commit('login', userInfo);
								this.$root.userInfo = JSON.parse(localStorage.getItem('userInfo'))
								this.postRequest('user/update', {
									wxopenid :this.openId,
									userId:resp.data.sessionUser.userId,
									token: resp.data.token,
								},false,true).then(res => {
									console.log(res)
									this.$router.push({
										name: 'Merchant',
										query: {
											sellerId: resp.data.sessionUser.typeCode
										}
									})
								})
								
							}

						} else {
							this.$dialog.alert({
								mes: resp.data.msg
							});
						}
					} else {
						this.$dialog.alert({
							mes: resp.data.msg
						});
					}
				})
			}
		}
	};
</script>
<style lang="scss">
	.yd-scrollview:after {
		height: 0;
	}
	
	.Login {
		input {
			font-family: 'PingFang-SC-Medium';
			font-size: 0.28rem;
		}
		height: 100%;
		background: url(../assets/imgs/bg@2x.png);
		background-size: 100% 100%;
		.logo {
			padding-top: 1.74rem;
			img {
				width: 4.78rem;
				height: 2.37rem;
			}
		}
		.form {
			width: 5.3rem;
			margin: 0 auto;
			padding-top: 1.2rem;
			.form-group {
				.yd-input {
					height: 0.46rem;
					font-size: 0.28rem;
				}
				border-bottom: 1px solid #C6C6C6;
				padding: 0.18rem 0;
				margin-bottom: 0.15rem;
				img.pswicon {
					width: 0.4rem;
					height: 0.46rem;
				}
			}
			.loginbtn {
				width: 100%;
				margin-top: 0.3rem;
				height: 0.78rem;
				background: #00A3FF;
				border-radius: 0.39rem;
				color: #fff;
				font-size: 0.31rem;
				text-align: center;
			}
		}
		.reg {
			padding-top: 2.5rem;
			width: 100%;
			text-align: center;
			span {
				color: #00A3FF;
			}
		}
		.forget {
			color: #626262;
			font-size: 0.24rem;
		}
	}
</style>


